<template>
  <div class="todo-list-wrapper">
    <div class="todo-list">
      <div class="list">
        <label class="todo">
          <input type="checkbox" class="check">
          <div class="done"></div>
          <div class="subject">숨쉬기</div>
        </label>
        <label class="todo">
          <input type="checkbox" class="check">
          <div class="done"></div>
          <div class="subject">아무것도 하지 않는 것을 하지 않도록 더욱 더 노력하는 것을 노력하도록 노력하기</div>
        </label>
        <label class="todo">
          <input type="checkbox" class="check">
          <div class="done"></div>
          <div class="subject">밥먹기</div>
        </label>
        <label class="todo">
          <input type="checkbox" class="check">
          <div class="done"></div>
          <div class="subject">잠먹기</div>
        </label>
        <label class="todo">
          <input type="checkbox" class="check">
          <div class="done"></div>
          <div class="subject">이닦고 잠 잘 준비 하기</div>
        </label>
        <label class="todo">
          <input type="checkbox" class="check">
          <div class="done"></div>
          <div class="subject">밥을 엄청나게 많이 먹기</div>
        </label>
        <label class="todo">
          <input type="checkbox" class="check">
          <div class="done"></div>
          <div class="subject">오늘도 할 일 없이 지내기</div>
        </label>
      </div>
      <div class="summary">
        <div class="task-count"> 할 일 :</div>
        <div class="remain-count"> 하지 않은 일 :</div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'TodoList'
  }
</script>

<style scoped lang="scss">
  .todo-list-wrapper {
    width: 100%;
    height: 100%;
    counter-reset: task remain;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #d9d9d9d9;
  }

  .todo-list {
    width: 300px;
    height: 350px;
    display: grid;
    grid-template-areas: "summary" "list";
    grid-template-rows: 50px minmax(0, 1fr);

    overflow: hidden;
    background: #ffffff;
    border-radius: 5px;
    border: 1px solid #a1a1a1;

    .list {
      grid-area: list;
      overflow: auto;

      .todo {
        counter-increment: task;
        display: block;
        padding: 10px;

        .subject {
          display: inline;
          color: #000000;

          transition: color .3s, background-size .3s;


          background-image: linear-gradient(transparent calc(100% - 2px), #a1a1a1 2px);
          background-repeat: no-repeat;
          background-size: 0 50%;
        }

        .done {
          display: inline-block;;
          position: relative;
          width: 15px;
          height: 10px;

          transition: width .3s;

          &:after, &:before {
            display: inline-block;
            position: absolute;
            content: '';

            width: 10px;
            height: 2px;
            top: 3px;
            left: 2px;

            background: #a1a1a1;
            transition: transform .3s;
          }
        }

        .check {
          position: absolute;
          visibility: hidden;

          &:not(:checked) {
            counter-increment: remain;
          }

          &:checked ~ {
            .done {
              width: 20px;

              &:after {
                left: 0;
              }

              &:before {
                left: 6px;

                --degree: 90deg;
              }

              &:after, &:before {
                background: #000000;
                transform: rotate(calc(45deg - var(--degree, 0deg)));
              }
            }

            .subject {
              color: #a1a1a1;
              background-size: 100% 50%;
            }

          }
        }

      }
    }

    .summary {
      grid-area: summary;
      display: flex;
      align-items: center;
      justify-content: space-evenly;
      border-bottom: 1px solid #a1a1a1;
      margin: 0 10px;

      .task-count {
        &:after {
          content: counter(task);
        }
      }

      .remain-count {
        &:after {
          content: counter(remain);
        }
      }
    }
  }
</style>



