<template>
  <div id="app">
    <div class="header">header</div>

    <div class="nav">
      <router-link v-for="route in routes" :key="route.name"
                   :to="{name: route.name}"
                   custom
                   v-slot="{ href, navigate, isActive, isExactActive }">
        <span class="link" :class="{
          'has-children':route.children,
          'router-link-active':isActive,
          on: isExactActive}">
          <a :href="href" @click="navigate">{{route.displayName}}</a>
          <router-link v-for="childRoute in route.children" :key="childRoute.name"
                       :to="{name: childRoute.name}"
                       custom
                       v-slot="{ href, navigate, isActive, isExactActive }">
            <span class="link"
                  :class="{'router-link-active':isActive, 'on': isExactActive}">
             <a :href="href" @click="navigate">{{childRoute.displayName}}</a>
            </span>
          </router-link>
        </span>
      </router-link>
    </div>

    <router-view class="content"/>
    <div class="footer"></div>
  </div>
</template>

<script>
  import _ from 'lodash'

  export default {
    name: 'App',
    data() {
      return {
        routes: []
      }
    },
    created() {
      this.routes = this.getRoute(this.$router.options.routes)
    },
    methods: {
      getRoute(routes) {
        if (!routes) {
          return
        }
        return _(routes)
          .filter(({meta}) => {
            const {hidden} = meta || {}
            return !hidden
          })
          .map(({name, meta, children: childrenRoutes}) => {
            const {name: displayName} = meta
            return {
              name,
              displayName,
              children: this.getRoute(childrenRoutes)
            }
          }).value()
      }
    }
  }
</script>

<style scoped lang="scss">
  #app {
    width: 100%;
    height: 100%;
  }
</style>



