<template>
  <div class="piece-wrapper">
    <div class="frame">
      <div class="piece" v-for="idx in pieces" :key="idx"></div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'piece',
    data() {
      return {
        pieces: 150
      }
    }
  }
</script>

<style scoped lang="scss">
  @use 'index.vue';

  $pieceX: 15;
  $pieceY: 10;
  $pieces: $pieceX * $pieceY;

  .piece-wrapper {
    width: 100%;
    height: 100%;

    display: flex;
    align-items: center;
    justify-content: center;
  }

  .frame {
    width: 300px;
    height: 200px;
    display: grid;
    grid-template-columns: repeat($pieceX, 1fr);
    grid-template-rows: repeat($pieceY, 1fr);

    .piece {
      width: 100%;
      height: 100%;
      background-image: url("https://picsum.photos/300/200");
      background-size: percentage($pieceX) percentage($pieceY);
      transition: transform (random()  + 0.5) * 1s;

      @for $idx from 1 through $pieces {
        &:nth-of-type(#{$idx}) {
          background-position: percentage(($idx - 1) % $pieceX / ($pieceX - 1)) percentage(floor(($idx - 1)/ $pieceX) / ($pieceY - 1));
          transform: translateX(( random() * 100 - 50 ) * 10%) translateY(( random() * 100 - 50 ) * 10%) rotate(random() * 1turn);
        }
      }
    }

    &:hover .piece {
      transform: translate(0, 0) rotate(0);
    }
  }
</style>



