<template>
  <i :class="[iConClass, shadow ? 'shadow' : '']" class="s-icon" :style="{
    fontSize,
    '--color': color,
    '--hover-color': adjustHoverColor
  }">
    <slot></slot>
  </i>
</template>

<script>
  export default {
    name: 'SIcon',
    props: {
      fontSize: {
        type: String
      },
      hoverColor: {
        type: String
      },
      color: {
        type: String
      },
      icon: {
        type: String
      },
      shadow: {
        type: Boolean
      }
    },
    computed: {
      iConClass() {
        return 'material-icons ' + this.icon
      },
      adjustHoverColor() {
        return this.hoverColor || this.color
      }
    }
  }
</script>
<style lang="scss">
  .s-icon {
    transition: .3s color;
    color: var(--color, #000000);

    &:hover {
      --text-shadow: #ffffff;
      color: var(--hover-color, #000000);
    }

    &.shadow {
      text-shadow: 0 0 1px var(--text-shadow, #000000);
    }
  }
</style>



