import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home'
import Examples from '@/pages/Examples'
import CardUi from '@/pages/Examples/CardUi'
import News from '@/pages/Examples/News'
import Piece from '@/pages/Examples/Piece'
import Timer from '@/pages/Examples/Timer'
import TodoList from '@/pages/Examples/TodoList'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      meta: {
        name: '홈'
      }
    },
    {
      path: '/examples',
      component: Examples,
      meta: {
        name: '예제'
      },
      children: [
        {
          path: 'card-ui/',
          name: 'CardUi',
          component: CardUi,
          meta: {
            name: '카드 UI'
          }
        },
        {
          path: 'piece/',
          name: 'Piece',
          component: Piece,
          meta: {
            name: '조각'
          }
        },
        {
          path: 'timer/',
          name: 'Timer',
          component: Timer,
          meta: {
            name: '시계'
          }
        },
        {
          path: 'todo-list/',
          name: 'TodoList',
          component: TodoList,
          meta: {
            name: '할 일 목록'
          }
        },
        {
          path: 'news/',
          name: 'News',
          component: News,
          meta: {
            name: '뉴스'
          }
        }
      ]
    },
    {
      path: '/card-ui',
      redirect: '/examples/card-ui/',
      meta: {hidden: true}
    },
    {
      path: '/piece',
      redirect: '/examples/piece/',
      meta: {hidden: true}
    },
    {
      path: '/timer',
      redirect: '/examples/timer/',
      meta: {hidden: true}
    },
    {
      path: '/todo-list',
      redirect: '/examples/todo-list/',
      meta: {hidden: true}
    },
    {
      path: '*',
      redirect: '/',
      meta: {hidden: true}
    }
  ],
  mode: 'history'
})



