<template>
  <div class="home">
    home
  </div>
</template>
<script>
  export default {
    name: 'Home',
    computed: {
      routes() {
        return this.$router.options.routes
      }
    }
  }
</script>
<style lang="scss" scoped>
  .home {
    width: 100%;
    height: 100%;
    .link {
      cursor: pointer;
      &.on {
        color: #0000ff;
      }
    }
  }
</style>



