<template>
  <div class="timer">


    <input type="checkbox" id="clock-starter" class="hidden clock-starter">
    <input type="checkbox" id="clock-init" class="hidden clock-init">

    <div class="clock-wrapper">
      <div class="clock">
        <div class="index index-12"></div>
        <div class="index index-3"></div>
        <div class="index index-6"></div>
        <div class="index index-9"></div>
        <div class="hand"></div>
      </div>
      <div class="btn-area">
        <label for="clock-starter" class="clock-starter-btn btn">

        </label>
        <label for="clock-init" class="clock-init-btn btn">
          초기화
        </label>
      </div>

    </div>
  </div>
</template>

<script>
  export default {
    name: 'Timer'
  }
</script>

<style scoped lang="scss">
  .timer {
    width: 100%;
    height: 100%;
  }
  .hidden {
    display: none;
  }

  .clock {
    position: relative;

    width: 200px;
    height: 200px;
    background: black;
    border: 3px solid black;
    border-radius: 50%;

    .index {
      position: absolute;
      display: inline-block;
      background: white;

      &-12 {
        min-width: 2.5%;
        height: 10%;
        left: 50%;
        top: 0;
        transform: translate(-50%, 0);
      }

      &-3 {
        min-width: 10%;
        height: 2.5%;
        right: 0;
        top: 50%;
        transform: translate(0, -50%);
      }

      &-6 {
        min-width: 2.5%;
        height: 10%;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
      }

      &-9 {
        min-width: 10%;
        height: 2.5%;
        left: 0;
        top: 50%;
        transform: translate(0, -50%);
      }
    }

    .hand {
      position: absolute;
      width: 2.5%;
      height: 20%;
      left: 48.75%;
      top: 30%;

      animation: rotate 6s linear infinite;
      transform-origin: 50% 100%;
      animation-play-state: paused;

      background: white;
    }
  }

  @keyframes rotate {
    100% {
      transform: rotateZ(360deg);
    }
  }

  .clock-wrapper {
    display: flex;
  }

  .btn-area {
    display: flex;
    flex-direction: column;
    width: 100px;
  }

  .btn {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 30px;
    background: red;
    color: white;
    border-radius: 5px;
    margin-bottom: 10px;
  }

  .clock-starter-btn {
    &::after {
      display: block;
      content: '시작';
    }
  }


  .clock-init:active ~ .clock-wrapper {
    .clock .hand {
      animation-name: unset;
    }
  }

  .clock-starter:checked ~ .clock-wrapper {
    .clock .hand {
      animation-play-state: running;
    }

    .btn-area .clock-starter-btn {
      background: blue;

      &::after {
        display: block;
        content: '정지';
      }
    }
  }
</style>



